	export default {
		data() {
			return {
				term_no: '',
				redBalls: [],
				blueBall:'',
				tableData:[
					{prizeType:'一等奖',amount: '9',total: '6057912.00'},
					{prizeType:'二等奖',amount: '120',total: '99179.00'},
					{prizeType:'三等奖',amount: '2196',total: '3000.00'},
					{prizeType:'四等奖',amount: '88763',total: '200.00'},
					{prizeType:'五等奖',amount: '1439570',total: '10.00'},
					{prizeType:'六等奖',amount: '14855930',total: '5.00'}
					
				]
			}
		},
		methods: {
			
		},
		onLoad(options) {
			var that = this;
            //that.data = JSON.parse(options.index); // 字符串转对象
            that.term_no = options.term_no;
			var arr = options.index.split(";");
			var redBalls = [];
			var blueBall = '';
			for (var i = 0; i <= arr.length-2; i++) {
				redBalls.push(arr[i]);
			}
			blueBall = arr[arr.length-1];
			that.redBalls=redBalls;
			that.blueBall=blueBall;
			console.log(that.redBalls);
			console.log(that.blueBall);
			
        }
	}
